  /* ==========================================================================
      Nutrition Label
  ============================================================================= */
  
  $font-regular: Arial, Helvetica, sans-serif;
  $font-black: "Arial Black", "Helvetica Black", "Arial-BoldMT";
  $tablet-up: "min-width: 767px";
  
  .nutrition-label {
    font-family: $font-regular;
    font-size: 12px;
    border: 2px solid #000000;
    padding: 5px;
    line-height: 1.2;
    width: 260px;
    margin-bottom: 15px;
    .nutrition-facts {
      font-family: $font-black;
      margin: 0 0 2px 0;
      padding-bottom: 2px;
      font-size: 28px;
      line-height: 1;
    }
    .nutrition-header {
      padding-bottom: 2px;
      margin-bottom: 2px;
    }
    .nutrition-footer {
      display: flex;
      font-size: 10px;
      margin-top: 5px;
      .asteric {
        width: 13px;
      }
    }
    .nutrition-row {
      display: flex;
      padding: 2px 0;
      >.nutrition-column {
        flex-grow: 1;
      }
    }
    .align-bottom {
      align-self: flex-end;
    }
    .border-b-none {
      border-bottom: 0;
    }
    .border-b {
      border-bottom: 1px solid #000000;
    }
    .border-b-md {
      border-bottom: 6px solid #000000;
    }
    .border-b-lg {
      border-bottom: 10px solid #000000;
    }
    .border-t-sm {
      border-top: 1px solid #000000;
    }
    .text-sm {
      font-size: 10px;
    }
    .text-md {
      font-size: 16px;
    }
    .text-bold {
      font-family: $font-black;
    }
    .text-right {
      text-align: right;
    }
    .text-indent {
      margin-left: 15px;
    }
    .text-indent-md {
      margin-left: 30px;
    }
    .calories {
      font-size: 25px;
      line-height: 1;
      font-family: $font-black;
      &.amount {
        font-size: 32px;
      }
    }
    &.vertical-label {
      width: 260px;
      @media screen and ($tablet-up) {
        width: 98%;
      }
      >.nutrition-row {
        padding: 0;
        display: block;
        @media screen and ($tablet-up) {
          display: flex;
        }
        >.nutrition-column {
          &:first-child {
            max-width: 100%;
            margin-right: 0;
            @media screen and ($tablet-up) {
              max-width: 200px;
              margin-right: 15px;
            }
          }
          &:nth-child(2) {
            >.nutrition-row {
              display: block;
              @media screen and ($tablet-up) {
                display: flex;
              }
            }
          }
          &:last-child {
            max-width: 100%;
            margin-left: 0;
            @media screen and ($tablet-up) {
              max-width: 100px;
              margin-left: 15px;
            }
          }
        }
        .nutrition-row {
          >.nutrition-column {
            &:first-child {
              margin-right: 0;
              @media screen and ($tablet-up) {
                margin-right: 15px;
              }
            }
          }
        }
      }
      .nutrition-header {
        padding-bottom: 0;
        margin-bottom: 0;
        @media screen and ($tablet-up) {
          padding-bottom: 2px;
          margin-bottom: 2px;
        }
      }
      .nutrition-footer {
        border-top: 6px solid #000000;
        margin-top: 2px;
        @media screen and ($tablet-up) {
          border-top: 0;
          margin-top: 18px;
        }
        .footnote,
        .asteric {
          padding-top: 5px;
          @media screen and ($tablet-up) {
            padding-top: 0;
          }
        }
      }
      .show-tablet {
        display: none;
        @media screen and ($tablet-up) {
          display: flex;
        }
      }
      .hide-tablet {
        display: block;
        @media screen and ($tablet-up) {
          display: none;
        }
      }
      .text-left-tablet {
        @media screen and ($tablet-up) {
          text-align: left;
        }
      }
      @media screen and ($tablet-up) {
        .border-b-none-tablet {
          border-bottom: 0;
        }
        .border-b-tablet {
          border-bottom: 1px solid #000000;
        }
        .border-b-md-tablet {
          border-bottom: 6px solid #000000;
        }
        .border-b-lg-tablet {
          border-bottom: 10px solid #000000;
        }
        .border-t-sm-tablet {
          border-top: 1px solid #000000;
        }
      }
    }
  }